<template>
    <el-card class="box-card">
        <div slot="header" class="clearfix header">
            <span>访客记录</span>
        </div>
        <div class="filter">
            <el-input placeholder="请输入内容" class="input-with-select" v-model="searchVal">
                <el-button slot="append" icon="el-icon-search" @click="searchHandler"></el-button>
            </el-input>

            <el-date-picker
            type="daterange"
            v-model="dateVal"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
            </el-date-picker>
        </div>
        <el-table
            :data="tableData"
            stripe
            style="width: 100%">
            <el-table-column label="编号" prop="id"></el-table-column>
            <el-table-column label="用户名" prop="username"></el-table-column>
            <el-table-column label="登录方式" prop="type">
                <template slot-scope="item">
                    <el-tag>{{item.row.type}}</el-tag>
                </template>
            </el-table-column>
            <el-table-column label="登录城市" prop="city"></el-table-column>
            <el-table-column label="登录时间" prop="login_time"></el-table-column>
            <el-table-column label="上一次登录时间" prop="prev_login_time"></el-table-column>
        </el-table>

        <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.currentPage"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="pagination.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pagination.total">
        </el-pagination>
    </el-card>
</template>
<script>
import {getHistoryDataApi} from '@/api/users.js'
export default {
    data() {
        return {
            pickerOptions: {
              shortcuts: [{
                text: '最近一周',
                onClick(picker) {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                  picker.$emit('pick', [start, end]);
                }
              }, {
                text: '最近一个月',
                onClick(picker) {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                  picker.$emit('pick', [start, end]);
                }
              }, {
                text: '最近三个月',
                onClick(picker) {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                  picker.$emit('pick', [start, end]);
                }
              }]
            },
            searchVal: '',
            dateVal: '',
            pagination: {
                total: 0,
                currentPage: 1,
                pageSize: 5,
            },
            tableData: []
        }
    },
    created() {
        getHistoryDataApi()
        .then(res => {
            this.tableData = res.data.list
            this.pagination.total = parseInt(res.data.total)
        })
    },
    methods: {
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pagination.pageSize = val
            getHistoryDataApi({
                pagenum: this.pagination.currentPage,
                pagesize: val
            })
            .then(res => {
                this.tableData = res.data.list
            })
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.pagination.currentPage = val
            getHistoryDataApi({
                pagenum: val,
                pagesize: this.pagination.pageSize
            })
            .then(res => {
                this.tableData = res.data.list
            })
        },
        searchHandler() {
            getHistoryDataApi({
                uname: this.searchVal
            })
            .then(res => {
                this.tableData = res.data.list
                this.pagination.total = parseInt(res.data.total)
            })
        }
    },
    watch: {
        dateVal(newData, oldData) {
            // console.log(newData);
            getHistoryDataApi({
                start_time: newData[0],
                end_time: newData[1]
            })
            .then(res => {
                this.tableData = res.data.list
                this.pagination.total = parseInt(res.data.total)
            })
        }
    }
}
</script>
<style scoped>
    .el-card .filter {
        margin-bottom: 20px;
        overflow: hidden
    }
    .el-card .filter .el-input {
        width: 300px;
        float: left
    } 
    .el-card .filter .el-date-editor {
        float: right;
    }
    .el-pagination {
        width: 100%;
        padding: 10px 0;
        background: #ebeef5;
        text-align: right;
        margin-top: 10px;
        padding-right: 10px;
        box-sizing: border-box;
    }
</style>